.registerContainer {
  position: fixed;
  left: 0px;
  bottom: 90px;
  background-image: linear-gradient(90deg, #af2896, #509bf5);
  height: 82px;
  width: 100%;
}

.registerWrapper {
  height: 100%;
  padding: 0px 30px 0px 30px;
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.registerAdv p {
  font-size: 16px;
  line-height: 22px;
  letter-spacing: 0.02em;
  color: white;
}

#registerTitle {
  font-size: 13px;
  text-transform: uppercase;
}

.registerAdvButton button {
  width: 280px;
  height: 38px;
  border-radius: 500px;
  font-weight: 600;
  letter-spacing: 1.8px;
  font-size: 13px;
  color: #2e77d0;
}

.registerAdvButton button:hover {
  transform: scale(1.1);
  cursor: pointer;
}
