.registerPopUpContainer {
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: 10;
  background-color: rgba(0, 0, 0, 0.9);
  display: flex;
  justify-content: center;
  align-items: center;
}

.registerPopUpQuit {
  position: absolute;
  z-index: -1;
  width: 100%;
  height: 100%;
}

.registerPopUpWrapper {
  height: 80%;
  width: 70%;
  min-width: 812px;
  background: #000
    url(https://open.scdn.co/cdn/images/improved-modal-bg.23e5dcb1.jpg) 50%
    no-repeat;
  background-size: 100% 100%;
  border-radius: 12px;
  padding: 65px 50px 50px;
  display: flex;
  justify-content: space-around;
  z-index: 2;
  animation: fadein 1s;
  -moz-animation: fadein 1s; /* Firefox */
  -webkit-animation: fadein 1s; /* Safari and Chrome */
  -o-animation: fadein 1s; /* Opera */
}

.registerPopUpLeft {
  width: 50%;
  color: white;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}
.registerPopUpLeft h1 {
  margin-bottom: 8px;
  font-size: 54px;
  font-weight: 700;
  line-height: 48px;
}

.registerPopUpLeft ul {
  margin: 0px;
  padding-left: 20px;
}

.registerPopUpLeft li {
  list-style: none;
  margin-top: 10px;
}
.registerPopUpLeft li:before {
  content: '\2714';
  margin-left: -20px;
  margin-right: 10px;
}

.registerPopUpLeft p {
  text-decoration: none;
  color: white;
  font-size: 14px;
  letter-spacing: 1px;
  font-weight: 500;
}

.registerPopUpLeft p:hover {
  width: max-content;
  border-bottom: 1px solid white;
  padding-bottom: 3px;
  cursor: pointer;
}

.registerPopUpRight {
  padding: 32px;
  background: #fff;
  height: 60%;
  width: 48%;
  border-radius: 12px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

.registerPopUpRight p {
  font-size: 12px;
}

.registerButton {
  width: 95%;
  height: 28%;
  border-radius: 500px;
  font-weight: 600;
  letter-spacing: 1.8px;
  font-size: 20px;
}

#register {
  color: white;
  background-color: #1db954;
}

#register:hover {
  filter: saturate(150%);
}
.solidDivisor {
  border-top: 1px solid #bbb;
  width: 90%;
}

#login {
  background-color: white;
  color: black;
  border: 1px solid black;
}

@keyframes fadein {
  from {
    opacity: 0;
    transform: translateY(5%);
  }
  to {
    opacity: 1;
  }
}
