#playlist-container {
  height: 100vh;
  overflow: scroll;
  overflow-x: hidden;
  -webkit-box-shadow: inset 0px 68px 70px 0px rgba(173, 173, 173, 0.65);
  -moz-box-shadow: inset 0px 68px 70px 0px rgba(173, 173, 173, 0.65);
  box-shadow: inset 0px 68px 70px 0px rgba(173, 173, 173, 0.65);
}

.playlist-wrapper {
  display: flex;
  padding: 30px;
}

.playlist-wrapper * {
  margin-bottom: 10px;
}

.playlist-left {
  width: 30%;
  display: flex;
  flex-direction: column;
  align-items: center;
  color: white;
}

.playlist-img {
  width: 100%;
}

.playlist-left-title {
  text-align: center;
  margin-bottom: 25px;
}

.playlist-left-title a {
  filter: brightness(50%);
  font-size: 14px;
  text-decoration: none;
  color: white;
}

.playlist-left-title a:hover {
  filter: brightness(70%);
  text-decoration: underline;
}
.playlist-left-buttons {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.playlist-left-buttons p {
  filter: brightness(60%);
  font-size: 14px;
}
.playlist-button {
  width: 70%;
  height: 40px;
  margin-bottom: 25px;
  border-radius: 500px;
  font-weight: 600;
  letter-spacing: 1.8px;
  font-size: 12px;
  color: white;
  background-color: #1db954;
}

.playlist-button:hover {
  filter: saturate(150%);
}

.playlist-fav {
  width: 50%;
  display: flex;
  justify-content: space-around;
}

.playlist-icon {
  width: 20px;
  height: 20px;
  filter: invert(90%);
}

.playlist-right {
  width: 100%;
  padding-left: 20px;
}

.song {
  padding: 10px;
  display: flex;
  width: 100%;
  color: white;
}

.song p {
  font-size: 15px;
  filter: brightness(80%);
}
.song:hover {
  background-color: rgba(100, 100, 100, 0.3);
  border-radius: 5px;
}

.song:hover .playlist-icon {
  filter: invert(100%) brightness(100%);
  content: url('https://image.flaticon.com/icons/svg/254/254434.svg');
}

.song:hover #song-more-icon {
  visibility: visible;
}

.song-name {
  width: 80%;
  padding-left: 10px;
}

.song-name h3 {
  font-size: 15px;
  font-weight: 500;
  margin-bottom: 2px;
}

.song-name a {
  display: inline;
  filter: brightness(70%);
  font-size: 14px;
  color: white;
  text-decoration: none;
}

.song-name a:hover {
  filter: brightness(100%);
  border-bottom: 1px solid white;
  cursor: pointer;
}

.song-more {
  width: 10%;
}

#song-more-icon {
  visibility: hidden;
  width: 20px;
  height: 20px;
  filter: invert(90%);
}

#song-more-icon:hover {
  transform: scale(1.1);
  cursor: pointer;
}
