.albumsContainer {
  display: flex;
  flex-direction: column;
  color: white;
  padding: 10px 32px 0px 32px;
}

.albumsRecoContainer {
  /* display: flex;
  width: 100%;
  justify-content: space-between; */
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(180px, 2fr));
  grid-gap: 20px;
  grid-auto-rows: minmax(180px, auto);
}

.albumsWrapper {
  padding: 16px 0px 16px 0px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}

.albumsHeader {
  display: flex;
  margin-bottom: 10px;
}
.albumsTitle {
  width: 90%;
}
.albumsTitle > h2 {
  display: inline;
  letter-spacing: 1.5px;
  cursor: pointer;
}

.albumsTitle > h2:hover {
  border-bottom: 1px solid white;
  padding-bottom: 3px;
}

.albumsMore > p {
  color: white;
  text-decoration: none;
  font-size: 13px;
  filter: brightness(70%);
  letter-spacing: 1px;
  font-weight: 600;
  text-transform: uppercase;
  cursor: pointer;
}

.albumsMore p:hover {
  border-bottom: 1px solid white;
  padding-bottom: 3px;
}

.albumsDescription {
  font-size: 14px;
  filter: brightness(60%);
  margin-bottom: 10px;
}

.albumContainer {
  background-color: rgb(40, 40, 40);
  display: flex;
  flex-direction: column;
  /* width: 167px;
  height: 240px; */
  border-radius: 5%;
  cursor: pointer;
  color: white;
  text-decoration: none;
}

.albumContainer:hover .playButtonContainer {
  display: inline;
}

.albumWrapper {
  height: 100%;
  padding: 20px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: baseline;
}

.albumWrapper img {
  width: 100%;
}

.albumName {
  padding: 20px 0px 10px 0px;
  font-size: 14px;
  font-weight: 800;
  letter-spacing: 0.2px;
}

.albumDescription {
  font-size: 12px;
  filter: brightness(60%);
}

.playButtonContainer {
  display: none;
  position: relative;
}

.playButtonWrapper {
  position: absolute;
  top: -55px;
  right: 15px;
}

.playButtonWrapper > input {
  height: 40px;
  width: 40px;
  filter: invert(80%);
  cursor: default;
}
.playButtonWrapper > input:hover {
  transform: scale(1.05);
}

::-webkit-scrollbar {
  width: 10px;
  height: 10px;
}

/* Track */
::-webkit-scrollbar-track {
  background: transparent;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: rgb(90, 90, 90);
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555;
}
